    *,*:after,*:before {
      box-sizing: inherit;
    }

    html {
      box-sizing: border-box;
      height: 100%;
    }

    body {
      color:#333;
      font: 0.875em/1.4 'Open Sans', sans-serif;
      text-rendering: optimizeLegibility;
    }

    section {
      max-width: 52em;
    }

    dl,ol,p,pre,ul {
      margin:1.5em 0;
    }

    ul {
      list-style: square;
      padding: 0 0 0 1.5em;
    }

    dt {
      font-weight: 700;
    }

    dd {
      margin: .4em 0 1em 2em;
    }

    h1,h2 {
      color:#777;
      font-weight: 300;
    }

    h1 {
      font-size: 2.5em;
      margin:0 0 0.6em;
    }

    h2 {
      font-size: 1.75em;
      margin: 1em 0;
    }

    h3 {
      color: #666;
      font-size: 1.25em;
    }
    h3.H3--underline {
      border-bottom: 1px solid hsl(30,10%,85%);
      margin:1.2em 0;
      padding-bottom: .5em;
    }

    i {
      display: inline-block;
      font: inherit;
    }

    a {
      color:hsl(16,87%,54%);
      text-decoration: none;
    }
    a.A--normal {
      font-weight: 400;
    }

    a:hover {
      text-decoration: underline;
    }

    b,strong {
      font-weight: 600;
    }

    code {
      background:#000000;
      background: rgba(0, 0, 0, 0.075);
      border-radius: 2px;
      color: #000;
      font-family: "Menlo", monospace;
      font-size: .9em;
      padding: 2px 5px 1px;
    }

    a code {
      color: inherit;
    }

    figure {
      margin: 0;
    }

    img {
      max-width: 100%;
      height: auto;
    }

    input::-webkit-input-placeholder {
      color: #aaa;
    }

    input::-moz-placeholder {
      color: #aaa;
    }

    input:-ms-input-placeholder {
      color: #aaa;
    }

    input::placeholder {
      color: #aaa;
    }

    table {
      border-collapse: collapse;
      border-spacing: 0;
    }

    @media (min-width: 1024px) {
      h1 {
        margin:0 0 0.8em;
      }
      h2 {
        margin:1.14286em 0;
      }
      h3.H3--underline {
        margin:1.6em 0;
      }
    }

