    }

    .hljs-attribute,.hljs-name,.hljs-tag {
      color: #000080;
      font-weight: normal;
    }

    .hljs-link,.hljs-regexp {
      color: #009926;
    }

    .hljs-bullet,.hljs-symbol {
      color: #990073;
    }

    .hljs-built_in,.hljs-builtin-name {
      color: #0086b3;
    }

    .hljs-meta {
      color: #999;
      font-weight: bold;
    }

    .hljs-deletion {
      background: #fdd;
    }

    .hljs-addition {
      background: #dfd;
    }

    .hljs-emphasis {
      font-style: italic;
    }

    .hljs-strong {
      font-weight: bold;
    }

