/********** CUSTOM RADIO BUTTON STYLES **********/
ul.radio {
  list-style: none;
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
}

ul.radio li {
  color: #AAAAAA;
  display: block;
  position: relative;
  float: left;
  width: 100%;
  height: auto;
  border-bottom: 1px dotted #f1f1f1;
}

ul.radio li input[type=radio] {
  position: absolute;
  visibility: hidden;
}

ul.radio li label {
  display: block;
  position: relative;
  font-weight: normal;
  font-size: 14px;
  padding-left: 40px;
  margin: 9px auto;
  height: 25px;
  z-index: 9;
  cursor: pointer;
  -webkit-transition: all 0.25s linear;
  margin-bottom: 5px;
}

ul.radio li:hover label {
  color: #535B65;
}

ul.radio li .check {
  display: block;
  position: absolute;
  border: 4px solid #AAAAAA;
  border-radius: 100%;
  height: 20px;
  width: 20px;
  top: 10px;
  left: 10px;
  z-index: 5;
  transition: border .25s linear;
  -webkit-transition: border .25s linear;
}

ul.radio li:hover .check {
  border: 4px solid #22A7F0;
}

ul.radio li .check::before {
  display: block;
  position: absolute;
  content: '';
  border-radius: 100%;
  height: 8px;
  width: 8px;
  top: 2px;
  left: 2px;
  margin: auto;
  transition: background 0.25s linear;
  -webkit-transition: background 0.25s linear;
}

input[type=radio]:checked ~ .check {
  border: 4px solid #22A7F0;
}

input[type=radio]:checked ~ .check::before {
  background: #22A7F0;
}

input[type=radio]:checked ~ label {
  color: #535B65;
}

/********** CUSTOM CHECKBOX BUTTON STYLES **********/
ul.checkbox {
  padding-left: 20px;
}

ul.checkbox li {
  margin-bottom: 20px;
}

ul.checkbox li ul li {
  margin-bottom: 0;
}

ul.checkbox label {
  padding-left: 0;
}

