.voyager-ga-embed html {
  font-family: sans-serif;
  /* 1 */
  -ms-text-size-adjust: 100%;
  /* 2 */
  -webkit-text-size-adjust: 100%;
  /* 2 */
}

.voyager-ga-embed body {
  margin: 0;
}

.voyager-ga-embed article, .voyager-ga-embed aside, .voyager-ga-embed details, .voyager-ga-embed figcaption, .voyager-ga-embed figure, .voyager-ga-embed footer, .voyager-ga-embed header, .voyager-ga-embed main, .voyager-ga-embed menu, .voyager-ga-embed nav, .voyager-ga-embed section, .voyager-ga-embed summary {
  /* 1 */
  display: block;
}

.voyager-ga-embed audio, .voyager-ga-embed canvas, .voyager-ga-embed progress, .voyager-ga-embed video {
  display: inline-block;
}

.voyager-ga-embed audio:not([controls]) {
  display: none;
  height: 0;
}

.voyager-ga-embed progress {
  vertical-align: baseline;
}

.voyager-ga-embed [hidden], .voyager-ga-embed template {
  display: none;
}

.voyager-ga-embed a {
  background-color: transparent;
  /* 1 */
  -webkit-text-decoration-skip: objects;
  /* 2 */
}

.voyager-ga-embed a:active, .voyager-ga-embed a:hover {
  outline-width: 0;
}

.voyager-ga-embed abbr[title] {
  border-bottom: none;
  /* 1 */
  text-decoration: underline;
  /* 2 */
  text-decoration: underline dotted;
  /* 2 */
}

.voyager-ga-embed b, .voyager-ga-embed strong {
  font-weight: inherit;
  font-weight: bolder;
}

.voyager-ga-embed dfn {
  font-style: italic;
}

.voyager-ga-embed h1 {
  font-size: 2em;
  margin: 0.67em 0;
}

.voyager-ga-embed mark {
  background-color: #ff0;
  color: #000;
}

.voyager-ga-embed small {
  font-size: 80%;
}

.voyager-ga-embed sub, .voyager-ga-embed sup {
