/**************************************************/
/***				BREADCRUMB CSS				***/
/**************************************************/
.breadcrumb-container {
  position: relative;
}

.breadcrumb.filemanager {
  top: 0px;
  background: #f0f0f0;
  border: 1px solid #E0E0E0;
  border-bottom: 0px;
  border-radius: 0px;
  padding-left: 20px;
  width: 100%;
  margin-top: 0px;
  left: 0px;
}

.breadcrumb.filemanager li {
  cursor: pointer;
  transition: color 0.1s linear;
  position: relative;
}

.breadcrumb.filemanager li:hover {
  color: #555;
}

.breadcrumb li .arrow {
  display: none;
  position: absolute;
  bottom: -14px;
  width: 12px;
  height: 12px;
  -ms-transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
  background: #f0f0f0;
  left: 50%;
  border-right: 1px solid #efefef;
  border-bottom: 1px solid #efefef;
}

.select2-display-none {
  z-index: 999999 !important;
}

.breadcrumb li:last-child .arrow {
  display: block;
}

.breadcrumb li:first-child .arrow {
  margin-left: -5px;
}

.breadcrumb li {
  color: #4DA7E8;
  cursor: pointer;
  font-weight: bold;
}

.breadcrumb li:last-child {
  color: #949494;
  cursor: pointer;
}

.breadcrumb-container .toggle {
  float: right;
  position: absolute;
  top: 11px;
  cursor: pointer;
  right: 5px;
  color: #bbb;
  transition: color 0.1s linear;
  overflow: visible;
}

.breadcrumb-container .toggle:hover {
  color: #aaa;
}

.breadcrumb-container .toggle span {
  font-size: 9px;
  text-transform: uppercase;
  float: left;
  top: 2px;
  position: relative;
  font-weight: bold;
  right: 10px;
}

.breadcrumb-container .toggle i {
  font-size: 18px;
  float: right;
  margin-right: 5px;
  position: relative;
  top: -4px;
}

.nothingfound {
  display: none;
}

#filemanager {
  position: relative;
  min-height: 200px;
}

#filemanager .loader {
  margin-top: 25px;
}

#filemanager #content {
  display: block;
  background: #fff;
}

.flex {
  display: flex;
  flex-wrap: wrap;
  border: 1px solid #E0E0E0;
  border-top: 0px;
}

.flex #left {
  flex: 4;
  position: relative;
  min-height: 230px;
}

.flex #left #no_files {
  display: none;
}

.flex #left #no_files h3 {
  text-align: center;
  margin-top: 55px;
  margin-bottom: 75px;
  color: #949494;
}

.flex #right {
  flex: 1;
  border-left: 1px solid #f1f1f1;
}

#right .right_details {
  display: block;
}

#right .right_none_selected {
  display: none;
  text-align: center;
}

#right .right_none_selected i {
  width: 100%;
  text-align: center;
  font-size: 30px;
  margin-left: 0px;
  padding: 50px;
  display: block;
  background: #f9f9f9;
}

#right .right_none_selected p {
  text-align: center;
  color: #bbb;
  padding: 10px;
  border-bottom: 1px solid #f1f1f1;
}

#files {
  display: flex;
  list-style: none;
  width: 100%;
  margin: 0px;
  padding: 0px;
  flex-wrap: wrap;
  padding: 10px;
  position: relative;
  -webkit-touch-callout: none;
  /* iOS Safari */
  -webkit-user-select: none;
  /* Chrome/Safari/Opera */
  -khtml-user-select: none;
  /* Konqueror */
  -moz-user-select: none;
  /* Firefox */
  -ms-user-select: none;
  /* Internet Explorer/Edge */
  user-select: none;
}

#files li {
  flex: 1;
  width: 100%;
  min-width: 200px;
  max-width: 250px;
}

#files li .file_link {
  background: #eee;
  padding: 10px;
  margin: 10px;
  cursor: pointer;
  border-radius: 3px;
  border: 1px solid #ecf0f1;
  overflow: hidden;
  background: #f6f8f9;
  display: flex;
}

#files li .file_link .details {
  flex: 2;
  overflow: hidden;
  width: 100%;
}

#files li .file_link .details small {
  font-size: 11px;
  position: relative;
  top: -3px;
}

#files li .file_link .link_icon {
  flex: 1;
}

#files li .file_link img, #files li .file_link .img_icon {
  display: none;
}

#files li .file_link.image img, #files li .file_link.image .img_icon {
  display: block;
}

#files li .file_link.image img {
  height: 50px;
}

#files li .file_link.image .img_icon {
  width: 50px;
  height: 50px;
  display: block;
}

#files li .file_link.selected, #files li .file_link:hover {
  background: #4da7e8 !important;
  border-color: #2581b8;
  color: #fff;
}

#files li .file_link.selected h4, #files li .file_link:hover h4 {
  color: #fff;
}

#files li .details h4 {
  margin-bottom: 2px;
  margin-top: 10px;
  max-height: 17px;
  height: 17px;
  overflow: hidden;
  font-size: 14px;
  text-overflow: ellipsis;
}

#files li .details.folder h4 {
  margin-top: 16px;
}

.file_link.folder i.icon {
  float: left;
  margin-left: 10px;
}

.file_link.folder .num_items {
  display: block;
}

.file_link .link_icon {
  text-align: center;
  padding-left: 0px;
  margin-left: 0px;
  margin-right: 5px;
}

.file_link .link_icon i {
  padding-left: 0px;
  padding-right: 0px;
  position: relative;
  top: 5px;
}

.file_link i.icon:before {
  font-size: 40px;
}

.detail_img {
  border-bottom: 1px solid #f1f1f1;
  background: #eee;
}

.detail_img img {
  width: 100%;
  height: auto;
  display: inline-block;
}

.detail_img i {
  display: block;
  width: 100%;
  text-align: center;
  font-size: 70px;
  margin-left: 0px;
  padding: 30px;
  background: #f9f9f9;
}

.detail_img.folder i.fa-folder {
  display: block;
}

.detail_img.file i.fa-file {
  display: block;
}

.detail_img.image img {
  display: block;
}

.detail_info {
  padding: 10px;
}

.detail_info .selected_file_count, .detail_info.folder .selected_file_size {
  display: none;
}

.detail_info.folder .selected_file_count {
  display: block;
}

.detail_info span {
  display: block;
  clear: both;
}

.detail_info a {
  color: #4DA7E8;
}

.detail_info .selected_file_count, .detail_info .selected_file_size {
  padding-top: 0px;
}

.detail_info h4 {
  float: left;
  color: #bbb;
  margin: 0px;
  font-size: 12px;
  margin-top: 3px;
  margin-right: 8px;
  padding-bottom: 2px;
  font-weight: 400;
}

.detail_info p {
  float: left;
  color: #444;
  padding-bottom: 3px;
  font-size: 12px;
  font-weight: 400;
}

/********** file upload progress **********/
#filemanager .progress {
  border-radius: 0px;
  margin-bottom: 0px;
}

#uploadProgress {
  display: none;
  background: #eee;
}

/********** end file upload progress **********/
#file_loader {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0px;
  top: 0px;
  background: rgba(255, 255, 255, 0.7);
  z-index: 9;
}

#file_loader #file_loader_inner {
  width: 60px;
  height: 60px;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-left: -30px;
  margin-top: -30px;
}

#file_loader .icon {
  font-size: 60px;
  width: 100%;
  height: 100%;
  line-height: 60px;
  -webkit-animation: spin 1.2s ease-in-out infinite;
  -moz-animation: spin 1.2s ease-in-out infinite;
  animation: spin 1.2s ease-in-out infinite;
}

#file_loader p {
  margin-top: 40px;
  position: absolute;
  text-align: center;
  width: 100%;
  top: 50%;
  font-weight: 400;
  font-size: 12px;
}

@-moz-keyframes spin {
  100% {
    -moz-transform: rotate(360deg);
  }
}

@-webkit-keyframes spin {
  100% {
    -webkit-transform: rotate(360deg);
  }
}

@keyframes spin {
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

